<template>
    <b-row>
      <b-col md="auto">
        <b-calendar v-model="value" @context="onContext" locale="en-US" selected-variant="warning" today-variant="warning" nav-button-variant="warning" initial-date="2022-08-14"></b-calendar>
      </b-col>
    </b-row>
  </template>
  
  <script>
    export default {
      data() {
        return {
          value: '',
          context: null
        }
      },
      methods: {
        onContext(ctx) {
          this.context = ctx
        }
      }
    }
  </script>

<style>
.b-calendar{
  width: 900vw;
}
</style>